@import '../variables';

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__logo {
    display: flex;

    img {
      margin-right: 15px;
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1%;
      text-transform: uppercase;
      font-weight: 800;
    }

    p {
      color: #7b7b7b;
    }

    @media (max-width: 580px) {
      img {
        width: 55px;
        margin-left: 25px;
      }
    }

    @media (max-width: 930px) {
      div {
        display: none;
      }
    }
  }

  &__cart-icon {
    margin-right: 5px;
    height: 18px;
    width: 18px;
  }

  @media (max-width: 580px) {
    padding: 20px 0;
  }

  @media (max-width: 680px) {
    padding-bottom: 80px;
  }
}
